// ********************
// SECTIONS
// ********************

.section,
.section--hero,
.section--large,
.section--small {
  position: relative;
}

.section {
  padding: 3.75rem 0;

  // Above 1024px;
  @media screen and (min-width: map-get($breakpoints-up, "large")) {
    padding: 7.5rem 0;
  }
}

.section--hero {
  padding: 0;
}

.section--large {
  padding: 5rem 0;

  // Above 1024px;
  @media screen and (min-width: map-get($breakpoints-up, "large")) {
    padding: 10rem 0;
  }
}

.section--small {
  padding-top: 2.5rem;
  padding-right: 0;
  padding-bottom: 0;
  padding-left: 0;

  // Above 1024px;
  @media screen and (min-width: map-get($breakpoints-up, "large")) {
    padding-top: 5rem;
    padding-right: 0;
    padding-bottom: 0;
    padding-left: 0;
  }
}

.is-gray {
  background-color: rgba($gray-400, 0.05);
}

.is-dark-gray {
  background-color: $gray-800;

  h2,
  h3,
  h4 {
    color: $white;
  }

  p {
    color: rgba($white, 0.8);

    strong {
      color: rgba($white, 1);
    }
  }
}

// ********************
// CONTAINERS
// ********************

.container {
  margin: 0 auto;
  width: 100%;

  > * {
    position: relative;
    z-index: 100;
  }

  // Below 639px;
  @media (max-width: map-get($breakpoints-down, "small")) {
    padding: 0 1rem;
  }

  // From 640px to 767px;
  @media (min-width: map-get($breakpoints-up, "small")) and (max-width: map-get($breakpoints-down, "medium")) {
    max-width: 40rem; // 640px
    padding: 0 2rem;
  }

  // From 768px to 1023px;
  @media (min-width: map-get($breakpoints-up, "medium")) and (max-width: map-get($breakpoints-down, "large")) {
    max-width: 48rem; // 768px
    padding: 0 3rem;
  }

  // From 1024px to 1279px;
  @media (min-width: map-get($breakpoints-up, "large")) and (max-width: map-get($breakpoints-down, "xl")) {
    max-width: 64rem; // 1024px
    padding: 0 4rem;
  }

  // From 1280px to 1535px;
  @media (min-width: map-get($breakpoints-up, "xl")) and (max-width: map-get($breakpoints-down, "2xl")) {
    max-width: 80rem; // 1280px
    padding: 0 5rem;
  }

  // Above 1535px
  @media (min-width: map-get($breakpoints-up, "2xl")) {
    max-width: 96rem; // 1536px
    padding: 0 6rem;
  }
}

// ********************
// HELMETS
// ********************

.helmet {
  margin: 0 auto;
  max-width: 65ch;
}
