// ********************
// CALLOUTS
// ********************

.callout {
  margin-left: auto;
  margin-right: auto;
  max-width: 48.125rem;
  padding: 5rem 2.5rem;
  border-radius: 0.75rem;
  box-shadow: 0px 25px 50px -12px rgba(0, 0, 0, 0.25);

  @include breakpointsUp(medium) {
    padding: 5rem;
  }

  hr {
    margin-top: 2.5rem;
    margin-bottom: 2.5rem;
    border-color: $gray-400;
    opacity: 0.1;
  }

  &.is-white {
    background-color: $white;
    border: 2px dashed $gold-400;
  }

  &.is-dark {
    background-color: $gray-500;
    color: rgba($white, 0.8);

    > h3,
    h4 {
      color: $white;
    }

    strong {
      color: rgba($white, 1);
    }

    hr {
      border-color: $white;
      opacity: 0.1;
    }
  }

  &.is-beige {
    background-color: rgba($beige-400, 0.5);
  }

  &.is-transparent {
    background-color: rgba($gray-400, 0.5);
    border: 2px dashed rgba($beige-400, 0.7);
  }
}

.callout__grid {
  grid-gap: 1.25rem;

  @include breakpointsUp(medium) {
    grid-gap: 2rem;
  }
}

.sub-checklist {
  padding: 0 1.25rem;
  max-width: 65ch;
  font-size: 0.875rem;
  line-height: 1.71;
  color: rgba($white, 0.5);

  @media (max-width: map-get($breakpoints-down, "small")) {
    font-size: 0.75rem;
    line-height: 1.67;
  }

  @media (min-width: map-get($breakpoints-down, "medium")) {
    font-size: 0.875rem;
    line-height: 1.71;
  }

  @media (min-width: map-get($breakpoints-down, "large")) {
    font-size: 1rem;
    line-height: 1.75;
  }

  @media (min-width: map-get($breakpoints-down, "xl")) {
    font-size: 1.125rem;
    line-height: 1.78;
  }
}

#sub-checklist__left,
#sub-checklist__right {
  margin-top: 0;
  margin-bottom: 0;

  li {
    position: relative;
    padding-left: 0.75rem;
    font-size: 0.875rem;

    &::before {
      content: "";
      position: absolute;
      top: 0.5rem;
      left: 0;
      width: 0.25rem;
      height: 0.25rem;
      border-radius: 50%;
      background-color: rgba($white, 0.8);
      background-image: none;
    }
  }
}

#sub-checklist__left {
  @include breakpointsUp(medium) {
    padding-right: 1rem;
  }
}

#sub-checklist__right {
  @include breakpointsUp(medium) {
    padding-left: 1rem;
  }
}
