a[data-hs-anchor="true"] {
  display: block;
  position: relative;
  visibility: hidden;

  @include breakpointsDown(medium) {
    top: 8rem;
  }

  @include breakpointsUp(medium) {
    top: 11rem;
  }
}

// ********************
// HERO SECTION
// ********************

#hero__video {
  --video-width: 100vw;

  width: var(--video-width);
  height: calc(0.5625 * var(--video-width));
}

.hero__video-wrapper {
  position: relative;
}

.hero__ribbon-wrapper {
  position: absolute;
  z-index: 200;
  top: -1px;
  right: 0;
  left: 0;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

#ribbon {
  padding: 0 1.5rem;
  width: 35.625rem;
  height: 2.5rem;
  background-color: #ce0000;
  border-bottom-left-radius: 0.5rem;
  border-bottom-right-radius: 0.5rem;
  cursor: pointer;
  box-shadow: 0px 10px 15px -3px rgba(0, 0, 0, 0.1),
    0px 4px 6px -2px rgba(0, 0, 0, 0.05);

  p {
    color: $white;
  }
}

// ********************
// PROOF SECTION
// ********************

.proof__logo {
  margin: 2.5rem 1.25rem;
  width: auto;
  height: 3rem;

  @include breakpointsDown(medium) {
    height: 1.5rem;
  }

  &.large {
    height: 4.5rem;

    @include breakpointsDown(medium) {
      height: 2.25rem;
    }
  }
}

// ********************
// EARLY BIRD SECTION
// ********************

.early-bird__date {
  text-transform: unset;
  color: $gold-500;
  margin-bottom: 2.5rem;

  @include breakpointsUp(medium) {
    margin-bottom: 5rem;
  }
}

.early-bird__price-wrapper,
.bonus__extra {
  max-width: 48.125rem;
  margin: 0 auto;
}

.early-bird__price-wrapper {
  text-align: center;
  margin-bottom: 5rem;
}

.early-bird__retail,
.early-bird__saving {
  font-size: 1.875rem;
  line-height: 2.25rem;
  font-weight: 500;
  text-transform: uppercase;

  @include breakpointsUp(medium) {
    font-size: 2.25rem;
    line-height: 2.5rem;
  }
}

.early-bird__retail {
  position: relative;
  display: inline-block;
  margin-bottom: 0.75rem;

  &::before {
    content: "";
    position: absolute;
    top: 50%;
    right: 0;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: red;
    transform: translateY(-50%);
    transform: skewY(-2deg);
  }
}

.early-bird__saving {
  margin-bottom: 1.875rem;
}

.early-bird__price {
  font-size: 4.5rem;
  line-height: 1;
  font-weight: 500;
  margin-bottom: 0.75rem;

  @include breakpointsUp(medium) {
    font-size: 6rem;
  }
}

.early-bird__expiration {
  text-transform: uppercase;
  font-weight: 500;
}

.bonus__extra__heading {
  margin-bottom: 1.875rem;
  text-align: center;
}

.bonus__extra__list {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  grid-template-rows: repeat(3, min-content(0, 1fr));
  gap: 1rem;
  justify-content: start;
  align-items: start;
}

.bonus__extra__list-item {
  position: relative;
  font-size: 0.875rem;
  line-height: 1.25rem;
  padding-left: 1.5rem;

  @include breakpointsUp(medium) {
    font-size: 1rem;
    line-height: 1.5rem;
    padding-left: 2rem;
  }

  &::before {
    content: "";
    position: absolute;
    top: 0.125rem;
    left: 0;
    width: 1rem;
    height: 1rem;
    background-image: url("static/img/check-icon.svg");
    background-repeat: no-repeat;
    background-size: cover;

    @include breakpointsUp(medium) {
      width: 1.125rem;
      height: 1.125rem;
    }
  }
}

// ********************
// TRANSFORM SECTION
// ********************

#transform__intro {
  margin-bottom: 1.25rem;

  @include breakpointsUp(medium) {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin-bottom: 5rem;

    > div {
      max-width: 50%;
    }
  }
}

#transform__intro__left {
  padding-bottom: 1.25rem;

  @include breakpointsUp(medium) {
    padding-right: 1.5rem;
  }
}

#transform__intro__right {
  @include breakpointsUp(medium) {
    padding-left: 1.5rem;
  }
}

#collage {
  width: 100%;
  height: auto;
  filter: drop-shadow(0 25px 50px rgba($gray-400, 0.25));
}

// ********************
// SCHEDULE SECTION
// ********************

.schedule__grid-row {
  display: grid;
  grid-template-columns: repeat(1, minmax(0, 1fr));
  grid-template-rows: repeat(2, minmax(0, min-content));
  place-items: center;
  gap: 3.75rem;

  @include breakpointsUp(large) {
    grid-template-columns: repeat(2, minmax(0, 1fr));
    grid-template-rows: repeat(1, minmax(0, 1fr));
    gap: 7.5rem;
  }

  &:not(:last-of-type) {
    margin-bottom: 2.5rem;

    @include breakpointsUp(medium) {
      margin-bottom: 5rem;
    }
  }
}

.schedule__grid-row__copy {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
}

.schedule__grid-row__text {
  &:not(:last-of-type) {
    margin-bottom: 1.25rem;
  }
}

.schedule__grid-row__eyebrow {
  margin-bottom: 1.25rem;
  font-size: 1.25rem;
  line-height: 1;
  font-weight: 700;
  text-transform: uppercase;
  color: $gold-400;
}

.schedule__grid__list {
  li {
    position: relative;
    padding-left: 1rem;
    font-weight: 700;

    &::before {
      content: "";
      position: absolute;
      top: 0.5rem;
      left: 0;
      width: 0.5rem;
      height: 0.5rem;
      background-color: #333333;
      border-radius: 100vw;
    }
  }

  li:not(:last-of-type) {
    margin-bottom: 0.625rem;
  }
}

#day1,
#day2,
#day3 {
  > :first-child {
    order: 2;
  }

  > :last-child {
    order: 1;
  }

  @include breakpointsUp(medium) {
    > :first-child {
      order: 1;
    }

    > :last-child {
      order: 2;
    }
  }
}

#day1-evening,
#day2-evening,
#day3-evening {
  > :first-child {
    order: 2;
  }

  > :last-child {
    order: 1;
  }
}

.schedule__grid-row__img {
  box-shadow: 0px 25px 50px -12px rgba(0, 0, 0, 0.25);
}

// ********************
// LEVEL UP SECTION
// ********************

#level__overlay {
  position: absolute;
  z-index: 50;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  backdrop-filter: brightness(80%) saturate(80%);

  @include breakpointsDown(small) {
    background-color: rgba($gray-500, 0.99);
  }

  @include breakpointsUp(small) {
    background-color: rgba($gray-500, 0);
    background-image: linear-gradient(
      to right,
      rgba($gray-500, 0),
      rgba($gray-500, 1) 55%
    );
  }
}

#level {
  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url("static/img/matt_level.webp"),
      url("static/img/matt_level.jpg");
    background-repeat: no-repeat;
    background-size: auto 125%;

    // Below 639px;
    @media (max-width: map-get($breakpoints-down, "small")) {
      background-position: -200vw -5vw;
    }

    // From 640px to 767px;
    @media (min-width: map-get($breakpoints-up, "small")) and (max-width: map-get($breakpoints-down, "medium")) {
      background-position: -180vw -5vw;
    }

    // From 768px to 1023px;
    @media (min-width: map-get($breakpoints-up, "medium")) and (max-width: map-get($breakpoints-down, "large")) {
      background-position: -95vw -5vw;
    }

    // From 1024px to 1279px;
    @media (min-width: map-get($breakpoints-up, "large")) and (max-width: map-get($breakpoints-down, "xl")) {
      background-position: -60vw -5vw;
    }

    // From 1280px to 1535px;
    @media (min-width: map-get($breakpoints-up, "xl")) and (max-width: map-get($breakpoints-down, "2xl")) {
      background-position: -35vw -5vw;
    }

    // Above 1535px
    @media (min-width: map-get($breakpoints-up, "2xl")) {
      background-position: -10vw -5vw;
    }
  }
}

#polaroid {
  padding-top: 0.875rem;
  padding-right: 0.875rem;
  padding-bottom: 3.5rem;
  padding-left: 0.875rem;
  background-color: $white;
  box-shadow: 0px 10px 15px -3px rgba($color: #000000, $alpha: 0.1),
    0px 2px 4px -1px rgba($color: #000000, $alpha: 0.06);
  transform: rotateZ(-5deg);

  img {
    width: 12.5rem;
    height: auto;
  }
}

// ********************
// WHO SECTION
// ********************

.tab {
  position: relative;
  width: 100%;
  padding: 1.5rem;
  border-radius: 0.25rem;
  transition: all ease-in-out 0.3s;
  cursor: pointer;

  &:hover {
    background-color: rgba($gray-400, 0.05);
  }

  &.is-active {
    background-color: $white;
    box-shadow: 0px 20px 25px -5px rgba($color: #000000, $alpha: 0.1),
      0px 10px 10px -5px rgba($color: #000000, $alpha: 0.04);
  }

  h3 {
    font-weight: 700;
  }
}

.is-active {
  @include breakpointsUp(medium) {
    &:after {
      content: "";
      position: absolute;
      right: 0;
      top: 50%;
      width: 0;
      height: 0;
      border: 1rem solid transparent;
      border-left-color: $white;
      border-right: 0;
      margin-top: -1rem;
      margin-right: -1rem;
    }
  }
}

.tab__content {
  padding: 2.5rem;
  border: 2px dashed $gold-400;
  border-radius: 0.5rem;
  background-color: $white;
  box-shadow: 0px 20px 25px -5px rgba($color: #000000, $alpha: 0.1),
    0px 10px 10px -5px rgba($color: #000000, $alpha: 0.04);
}

// ********************
// READY SECTION
// ********************

#ready {
  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url("static/img/matt_ready.webp"),
      url("static/img/matt_ready.jpg");
    background-repeat: no-repeat;
    background-size: auto 200%;
    background-position: center;
  }
}

#ready__overlay {
  position: absolute;
  z-index: 50;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(
    to right,
    rgba($gray-500, 1),
    rgba($gray-500, 1)
  );
  backdrop-filter: brightness(60%) saturate(60%);

  @include breakpointsUp(small) {
    background-image: linear-gradient(
      to right,
      rgba($gray-500, 1) 25%,
      rgba($gray-500, 0.5) 50%,
      rgba($gray-500, 1) 75%
    );
  }
}

.cta {
  width: 100%;
  padding: 5rem 2.5rem;
  background-color: rgba($white, 0.1);
  border: 2px dashed $gold-400;
  border-radius: 0.5rem;

  h3 {
    color: $white;
  }
}

// ********************
// BONUS SECTION
// ********************

.bonus__wrapper {
  display: grid;
  grid-template-columns: repeat(1, minmax(0, 1fr));
  grid-template-rows: repeat(3, minmax(0, min-content));
  gap: 2.5rem;
  align-items: start;
  margin-bottom: 2.5rem;

  @include breakpointsUp(medium) {
    grid-template-columns: repeat(2, minmax(0, 1fr));
    grid-template-rows: repeat(2, minmax(0, min-content));
    margin-bottom: 5rem;
  }

  @include breakpointsUp(large) {
    grid-template-columns: repeat(3, minmax(0, 1fr));
    grid-template-rows: repeat(1, minmax(0, min-content));
  }
}

.bonus__card {
  width: 100%;
  padding: 1.25rem;
  background-color: $white;
  border: 2px dashed $gold-500;
  border-radius: 0.5rem;
  box-shadow: 0px 25px 50px -12px rgba(0, 0, 0, 0.25);

  @include breakpointsUp(medium) {
    padding: 2.5rem;
  }
}

.bonus__img-wrapper {
  position: relative;
  width: 100%;
  height: auto;
  border-radius: 0.25rem;
  overflow: hidden;

  &::before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba($gray-400, 0.2);
    backdrop-filter: brightness(90%) saturate(90%);
  }
}

.bonus__img {
  width: 100%;
  height: 100%;
}

.bonus__card__heading {
  font-size: 1.25rem;
  line-height: 1.75rem;
  margin-bottom: 0.875rem;

  @include breakpointsUp(medium) {
    font-size: 1.5rem;
    line-height: 2rem;
    margin-bottom: 1.25rem;
  }
}

.bonus__extra__copy {
  &.is-centered {
    text-align: center;
  }
}

// ********************
// INVEST SECTION
// ********************

