#invest__overlay {
  position: absolute;
  z-index: 50;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  backdrop-filter: brightness(70%) saturate(70%);

  @include breakpointsDown(small) {
    background-color: rgba($gray-500, 0.99);
  }

  @include breakpointsUp(small) {
    background-color: rgba($gray-500, 0);
    background-image: linear-gradient(
      to right,
      rgba($gray-500, 1) 50%,
      rgba($gray-500, 0)
    );
  }
}

#pricing-card hr {
  border-color: rgba($white, 0.5);
}

.pricing__checklist-item {
  color: rgba($white, 0.8);

  strong {
    color: rgba($white, 1);
  }
}

.pricing__bottom {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  p {
    margin-top: unset;
    margin-bottom: unset;
  }
}

.pricing__price {
  font-size: 3rem;
  line-height: 1;
  font-weight: 700;
}

#price {
  margin-bottom: 2.5rem;
  color: rgba($white, 1);
}

// ********************
// FAQ SECTION
// ********************

.faq,
.who {
  cursor: pointer;
  width: 100%;

  > div {
    width: 100%;
    padding: 1.5rem;
    border-bottom: 1px solid rgba($gray-400, 0.05);
    transition: color ease-in-out 0.3s;

    &:hover h4 {
      color: $gold-400;
    }
  }

  h4 {
    padding-right: 2rem;
    font-size: 1.25rem;
    line-height: 1.125;
    font-weight: 500;
  }

  p {
    padding: 1.25rem;
    background-color: rgba($gray-400, 0.05);
  }
}

.faq img,
.who img {
  width: 2rem;
  height: 2rem;
  transition: transform 0.3s ease-in-out;

  &.is-rotated {
    transform: rotateZ(180deg);
  }
}

// ********************
// FINAL SECTION
// ********************

#final {
  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: url("static/img/matt_final.webp"),
      url("static/img/matt_final.jpg");
    background-repeat: no-repeat;
    background-size: auto 125%;

    // Below 639px;
    @media (max-width: map-get($breakpoints-down, "small")) {
      background-position: left -135vw bottom -30vw;
    }

    // From 640px to 767px;
    @media (min-width: map-get($breakpoints-up, "small")) and (max-width: map-get($breakpoints-down, "medium")) {
      background-position: left -90vw bottom -15vw;
    }

    // From 768px to 1023px;
    @media (min-width: map-get($breakpoints-up, "medium")) and (max-width: map-get($breakpoints-down, "large")) {
      background-position: left -70vw bottom -15vw;
    }

    // From 1024px to 1279px;
    @media (min-width: map-get($breakpoints-up, "large")) and (max-width: map-get($breakpoints-down, "xl")) {
      background-position: left -50vw bottom -10vw;
    }

    // From 1280px to 1535px;
    @media (min-width: map-get($breakpoints-up, "xl")) and (max-width: map-get($breakpoints-down, "2xl")) {
      background-position: left -30vw bottom -10vw;
    }

    // Above 1535px
    @media (min-width: map-get($breakpoints-up, "2xl")) {
      background-position: left -10vw bottom -5vw;
    }
  }
}

#final__overlay {
  position: absolute;
  z-index: 50;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  backdrop-filter: brightness(80%) saturate(80%);

  @include breakpointsDown(small) {
    background-color: rgba($gray-500, 0.99);
  }

  @include breakpointsUp(small) {
    background-color: rgba($gray-500, 0);
    background-image: linear-gradient(
      to right,
      rgba($gray-500, 0),
      rgba($gray-500, 1) 45%
    );
  }
}

#final picture {
  > * {
    position: relative;
    z-index: 100;
  }
}

.signature {
  width: auto;
  height: 4rem;
  margin-top: 0;
  margin-right: auto;
  margin-bottom: 2.5rem;
  margin-left: auto;
  filter: invert(100%) contrast(200%) brightness(100%);
  opacity: 0.5;
  mix-blend-mode: screen;
}
