// ********************
// HEADER
// ********************

header {
  position: fixed;
  z-index: 999;
  top: 0;
  right: 0;
  left: 0;
  background-color: $gray-500;
}

#header__top {
  height: 8.5rem;
}

#header__brand {
  > img {
    width: auto;
    height: 3.75rem;
  }
}

#header__bottom {
  position: relative;
  height: 3rem;

  &::before {
    content: "";
    position: absolute;
    left: -100vw;
    right: 0;
    width: 200vw;
    height: 1px;
    background-color: rgba($white, 0.5);
  }
}

// ********************
// DESKTOP NAV
// ********************

nav {
  height: 100%;
}

#navigation {
  @include breakpointsDown(large) {
    justify-content: space-between;

    > a {
      margin: 0 0.5rem;
    }
  }
}

.navigation__link {
  opacity: 0.5;
  margin: 0 2.5rem;
  transition: opacity 0.3s ease-in-out;
  font-size: 0.875rem;
  line-height: 1;
  text-align: center;
  color: $white;

  &:hover {
    opacity: 1;
  }
}

// ********************
// MOBILE NAV
// ********************

#hamburger__icon {
  width: 3rem;
  height: 100%;
  cursor: pointer;
}

#hamburger__icon > img {
  opacity: 0.8;
  transition: opacity 0.3s ease-in-out;
  width: 2.5rem;
  height: 2.5rem;

  &:hover {
    opacity: 1;
  }
}

#hamburger__menu {
  position: fixed;
  top: 8rem;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100vw;
  height: calc(100vh - 8rem);
  background-color: rgba($gray-400, 0.98);
  backdrop-filter: blur(4px) saturate(110%);
}

.hamburger__link {
  opacity: 0.5;
  padding: 2.5rem;
  transition: opacity 0.3s ease-in-out;
  font-size: 1.25rem;
  line-height: 1;
  color: rgba($white, 0.95);

  &:hover {
    opacity: 1;
  }
}

#hamburger__cta {
  margin-top: 2.5rem;
  margin-left: 2.5rem;
}
