#countdown {
  position: fixed;
  z-index: 800;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100vw;
  border-top: 1px solid $gray-100;
  padding: 1.25rem 0;
  background-color: rgba($white, 0.9);
  backdrop-filter: blur(4px) saturate(110%);

  &.is-removed {
    display: none;
  }
}

.countdown__header {
  font-size: 1rem;
  line-height: 1.5;
  font-weight: 700;
  text-transform: uppercase;
  margin-bottom: 0.625rem;
  color: $white;
}

.countdown__close {
  position: absolute;
  right: 2.5%;
  display: grid;
  place-items: center;
  width: 3rem;
  height: 3rem;
}

.countdown__wrapper {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.days__wrapper,
.hours__wrapper,
.minutes__wrapper,
.seconds__wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 0 1rem;
}

.days,
.hours,
.minutes,
.seconds {
  font-size: 1.875rem;
  line-height: 2.25rem;
  font-weight: 700;
  color: $gray-900;
}

.days__label,
.hours__label,
.minutes__label,
.seconds__label {
  font-size: 0.875rem;
  line-height: 1.25rem;
  color: $gray-300;
  text-transform: uppercase;
}

.countdown__wrapper--small {
  height: 100%;

  > p {
    margin-bottom: 0.25rem;
    color: $white;
  }

  > h3 {
    font-weight: 500;
    text-transform: uppercase;
    color: rgba($white, 0.5);
  }
}

.countdown__wrapper--small {
  width: 4rem;
}
