// ********************
// RESPONSIVE
// ********************

// RESPONSIVE UTILITY CLASSES
.is-hidden-for-mobile {
  @include breakpointsDown(medium) {
    display: none;
  }
}

.is-hidden-for-small-screens {
  @include breakpointsDown(large) {
    display: none;
  }
}

.is-hidden-for-large-screens {
  @include breakpointsUp(large) {
    display: none;
  }
}

.is-hidden-for-desktop {
  @include breakpointsUp(medium) {
    display: none;
  }
}

.is-hidden {
  display: none;
}

// ********************
// MARGINS
// ********************

// MARGIN RIGHT
.mr-4 {
  margin-right: 0.25rem;
}

.mr-8 {
  margin-right: 0.5rem;
}

.mr-16 {
  margin-right: 1rem;
}

// MARGIN BOTTOM
.mb-4 {
  margin-bottom: 0.25rem;
}

.mb-8 {
  margin-bottom: 0.5rem;
}

.mb-10 {
  margin-bottom: 0.625rem;
}

.mb-16 {
  margin-bottom: 1rem;
}

.mb-20 {
  margin-bottom: 1.25rem;
}

.mb-40 {
  margin-bottom: 2.5rem;
}

.mb-80 {
  margin-bottom: 5rem;
}

// MARGIN Y-AXIS
.my-80 {
  margin: 5rem 0;
}

// ********************
// PADDING
// ********************

.pt-60 {
  padding-top: 3.75rem;

  @include breakpointsDown(medium) {
    padding-top: 1.875rem;
  }
}

.pb-120 {
  padding-bottom: 7.5rem;

  @include breakpointsDown(medium) {
    padding-bottom: 3.5rem 0;
  }
}

// PADDING Y-AXIS
.py-40 {
  padding: 2.5rem 0;
}

.py-80 {
  padding: 5rem 0;

  @include breakpointsDown(medium) {
    padding: 2.5rem 0;
  }
}

.py-120 {
  padding: 7.5rem 0;

  @include breakpointsDown(medium) {
    padding: 5rem 0;
  }
}

.py-160 {
  padding: 10rem 0;

  @include breakpointsDown(medium) {
    padding: 7.5rem 0;
  }
}

// ********************
// BOX SHADOWS
// ********************

.shadow-sm {
  box-shadow: 0px 1px 2px 0px rgba($color: #000000, $alpha: 0.05);
}

.shadow-med {
  box-shadow: 0px 1px 3px 0px rgba($color: #000000, $alpha: 0.1),
    0px 1px 2px 0px rgba($color: #000000, $alpha: 0.06);
}

.shadow-lg {
  box-shadow: 0px 10px 15px -3px rgba($color: #000000, $alpha: 0.1),
    0px 2px 4px -1px rgba($color: #000000, $alpha: 0.06);
}

.shadow-xl {
  box-shadow: 0px 20px 25px -5px rgba($color: #000000, $alpha: 0.1),
    0px 10px 10px -5px rgba($color: #000000, $alpha: 0.04);
}

.shadow-2xl {
  box-shadow: 0px 25px 50px -12px rgba($color: #000000, $alpha: 0.25);
}

// ********************
// FONT SIZES
// ********************

.text-xs {
  font-size: 0.75rem;
  line-height: 1rem;
}

.text-sm {
  font-size: 0.875rem;
  line-height: 1.25rem;
}

.text-base {
  font-size: 1rem;
  line-height: 1.5rem;
}

.text-lg {
  font-size: 1.125rem;
  line-height: 1.75rem;
}

.text-xl {
  font-size: 1.25rem;
  line-height: 1.75rem;
}

.text-2xl {
  font-size: 1.5rem;
  line-height: 2rem;
}

.text-3xl {
  font-size: 1.875rem;
  line-height: 2.25rem;
}

.text-4xl {
  font-size: 2.25rem;
  line-height: 2.5rem;
}

.text-5xl {
  font-size: 3rem;
  line-height: 1;
}

.text-6xl {
  font-size: 3.75rem;
  line-height: 1;
}

.text-7xl {
  font-size: 4.5rem;
  line-height: 1;
}

.text-8xl {
  font-size: 6rem;
  line-height: 1;
}

.text-9xl {
  font-size: 8rem;
  line-height: 1;
}

// ********************
// TEXT TRACKING
// ********************

.tracking-tighter {
  letter-spacing: -0.05em;
}

.tracking-tight {
  letter-spacing: -0.025em;
}

.tracking-normal {
  letter-spacing: 0em;
}

.tracking-wide {
  letter-spacing: 0.025em;
}

.tracking-wider {
  letter-spacing: 0.05em;
}

.tracking-widest {
  letter-spacing: 0.1em;
}

.text-center {
  text-align: center;
}
